.header {
  height: 64px;
  border-radius: 0 0 8px 8px;
  margin-bottom: 40px;
  padding: 0 20px;
}

.header.light-theme {
  background: var(--light-text-color);
  border: 1.5px solid var(--main-text-color);
}

.header.dark-theme {
  background: var(--main-text-color);
  border: 1.5px solid var(--light-text-color);
}

.header > .container {
  height: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  cursor: pointer;
  align-items: center;
  display: flex;
}

.text-logo {
  width: 109px;
  height: 28px;
  cursor: pointer;
  color: var(--main-text-color);
  text-decoration: none;
}

.logo-image.light-theme {
  color: var(--main-text-color);
}

.logo-image.dark-theme {
  color: var(--light-text-color);
}

.header-links {
  align-items: center;
  gap: 24px;
  list-style-type: none;
  display: none;
}

.link-text {
  color: var(--main-text-color);
  align-items: center;
  text-decoration: none;
  display: flex;
}

.link-text.light-theme {
  color: var(--main-text-color);
}

.link-text.dark-theme {
  color: var(--light-text-color);
}

.link-text.shopping-link:before {
  content: "";
  width: 20px;
  height: 20px;
  background-size: 20px;
  margin-right: 8px;
  display: inline-block;
}

.sprite-image.light-theme {
  background-image: url("icons.f2119150.svg#icon-shop");
  background-repeat: no-repeat;
}

.sprite-image.dark-theme {
  fill: var(--light-text-color);
  background-image: url("icons.f2119150.svg#icon-shop-dark");
  background-repeat: no-repeat;
}

.switcher {
  width: 40px;
  height: 24px;
  background: linear-gradient( 180deg, var(--extra-bg-color) 0%, var(--light-text-color-60) 100% );
  cursor: pointer;
  border-radius: 12px;
  margin-left: auto;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switcher.active {
  background: linear-gradient(180deg, #4f2ee8 0%, var(--sub-text-color) 100%);
}

.switcher-handle {
  width: 20px;
  height: 20px;
  background: -20px 0 no-repeat;
  background-color: var(--light-text-color);
  border-radius: 50%;
  transition: left .3s ease-in-out;
  position: relative;
  top: 2px;
  left: 2px;
}

.switcher-circle {
  width: 18px;
  height: 18px;
  fill: var(--light-text-color);
  position: absolute;
  top: 3px;
  left: 1px;
}

.switcher.active .switcher-handle {
  background-position: 0 0;
  left: calc(100% - 22px);
}

.user-profile {
  width: 184px;
  height: 48px;
  cursor: pointer;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-color: var(--extra-bg-color);
  border: 1.5px solid var(--main-text-color);
  color: var(--light-text-color);
  border-radius: 18px;
  justify-content: space-around;
  margin-left: 24px;
  padding: 14px;
  transition: all .25s;
  display: none;
  position: relative;
}

@media screen and (min-width: 768px) {
  .user-profile.is-active {
    display: flex;
  }

  .custom-profile {
    display: none;
  }
}

